.tease{

    grid-row: 2 / 3;
    grid-column: 2 / 4;

    color: white;
    text-transform: uppercase;
    font-family: $font-secondary;

    @media screen and (max-width: $bp-small){
        grid-column: 2 / 5;
    }
    @media screen and (max-width: $bp-smallest){
        grid-column: 2 / 6;
    }


    &__name{
        font-size: 3rem;
        letter-spacing: .5rem;
    }

    &__more{
        display: flex;
        align-items: center;


        @media screen and (max-width: $bp-small){
            flex-direction: column;
            align-items: start;
        }

    }
    &__hook{
        font-size: 3.5rem;
        letter-spacing: .2rem;

        
        @media screen and (max-width: $bp-super-large){
            font-size: 3rem;
        }

        @media screen and (max-width: $bp-large-medium){
            font-size: 2.5rem;
        }
        @media screen and (max-width: $bp-large){
            font-size: 2rem;
        }
        @media screen and (max-width: $bp-medium){
            font-size: 1.5rem;
        }

    }

    &__btn{
        margin-left: 2rem;
        cursor: pointer;

        @media screen and (max-width: $bp-medium){
            margin-left: 1rem;
        }

        @media screen and (max-width: $bp-small){
            margin: 0;
        }
    }

    


}

