.container{
    
    display: grid;

    grid-template-rows: repeat(2, 100vh) repeat(4, min-content);

       grid-template-columns: 
       [full-start] minmax(6rem, 1fr) [center-start] repeat(8, [col-start] minmax(min-content, 14rem) 
       [col-end]) [center-end] minmax(6rem, 1fr) [full-end];




       @media screen and (max-width: $bp-medium-large){
        grid-template-rows: repeat(1, 100vh) repeat(4, min-content);
    }
}