.gallery{
 
    grid-column: full-start / full-end;
    grid-row: 6 / 7;
    display: grid;
    grid-template-columns: repeat(8, 1fr);
    grid-template-rows: repeat(8, 5vw);
    grid-gap: 1.5rem;
    padding: 1.5rem;

    &__heading{
        grid-row:  1 / 2;
        grid-column: 1 / -1;
        text-align: center;
    }
    &__item{


        &--1{
            grid-row: 2 / 4;
            grid-column: 1 / 3;
        }
        &--2{
            grid-row: 2 / 5;
            grid-column: 3 / 6;
        }
        &--3{
            grid-row: 2 / 4;
        }
        &--4{
            grid-column: 7 / 9;
            grid-row: 2 / 4;
        }
        &--5{
            grid-row: 4 / 7;
            grid-column: 1 / 3;
        }
        &--6{
            grid-row: 5 / 7;
            grid-column: 3 / 5;
        }
        &--7{
            grid-row: 5 / 6;
            grid-column: 5 / 6;
        }
        &--8{
            grid-row: 4 / 6;
            grid-column: 6 / 8;
        }
        &--9{
            grid-row: 4 / 7;
            grid-column: 8 / 9;
        }
        &--10{
            grid-row: 7 / 9;
            grid-column: 1 / 2;
        }
        &--11{
            grid-row: 7 / 9;
            grid-column:  2 / 4;
        }
        &--12{
            grid-row: 7 / 9;
            grid-column: 4 / 5;
        }
        &--13{
            grid-row: 6 / 9;
            grid-column: 5 / 8;
        }
        &--14{
            grid-row: 7 / 9;
            grid-column: 8 / 9;
        }
    }

    &__img{
        width: 100%;
        height: 100%;
        object-fit: cover;
        display: block;
    }
}