.story{

    &__heading{
        font-size: 2rem;
        font-weight: 300;

    }

    &__pictures{
        grid-row: 5 / 6 ;

        grid-column: full-start / col-end 4;

        display: grid;

        grid-template-rows: repeat(6, 1fr);
        grid-template-columns: repeat(6, 1fr);


        align-items: center;

        background-image: url("../img/testimonial-pic--1.jpg");
        background-size: cover;
        background-position: center;

    
        

     
      
    }

    &__img--1{
        width: 100%;
        grid-row: 2 / 6;
        grid-column: 2 / 6;

        box-shadow: 0 2rem 5rem rgba(black, .1);

   
       
    }
    &__img--2{
        width: 115%;
        grid-row: 4 / 6;
        grid-column: 4 / 7;
        z-index: 20;

        box-shadow: 0 2rem 5rem rgba(black, .2);

  
       

       


    }
    
    &__content{
        grid-row: 5 / 6 ;
        background-color: $color-grey-light-1;
     
        grid-column: col-start 5 / full-end;

        padding: 6rem 8vw;

        //alternate way

        // display: flex;
        // flex-direction: column;
        // justify-content: center;
        // align-items: flex-start;


        display: grid;
        align-content: center;
        justify-items: start;


    

    }

    &__text{
        font-size: 1.5rem;
        font-style: italic;
    }
}